@media ( max-width:1350px )
  .mini-slider__slider-wrapper
    width: 850px

@media ( max-width:1300px )
  .header__link--logo-index
    transform: translateX(-40px)
  .main-slider__container
    justify-content: center
  .main-slider__head
    font-size: 90px
  .main-slider__btn-next
    right: calc(50% - 547px)
  .mini-slider__slider-wrapper
    width: 800px

@media ( max-width:1200px )
  .header__link--logo-index
    transform: none
  .main-slider__head
    font-size: 85px
  .main-slider__slide::before
    left: -40px
  .slider-aside
    left: 0px
  .slider-aside__content
    margin-left: 30px
  .mini-slider__slider-wrapper
    width: 730px

@media ( max-width:1100px )
  .header__link--logo-index
    transform: none
  .main-slider__container
    margin-left: 40px
  .main-slider__head
    font-size: 80px
  .main-slider__btn-prev
    left: 7%
  .main-slider__btn-next
    right: 5%
  .slider-aside__content
    margin-left: 15px
  .main-slider__slide::before
    left: -55px
  .mini-slider__container
    justify-content: space-between
  .mini-slider__intro
    width: 28%
  .mini-slider__slider
    max-width: 700px

  .mini-slider__slider-wrapper
    position: static
    width: 64%
